import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col, Button, Tab, Tabs, Nav } from "react-bootstrap";
import { Navigate, useNavigate, useParams } from "react-router-dom";
import Counter from "../component/Counter";
import { useState, useEffect } from "react";
import styled, { css } from "styled-components";
import { useDispatch } from "react-redux";
import { addItem } from "../store/cartSlice";

let OrangeBtn = styled.button`
  background: orange;
  color: white;
  padding: 7px;
  border-radius: 5px;
  margin-left: 5px;
  border: none;
  position: relative;
  top: 2px;
`;

const Detail = (props) => {
  let dispatch = useDispatch(); //  
  let Navigate = useNavigate();
  let [tab, setTab] = useState(0);
  let [num, setNum] = useState("");

  useEffect(() => {
    //  detail  
    //console.log(choose.id)//  id 
    let takeOut = localStorage.getItem("watched"); //2.watched 
    //3. localStorage 
    takeOut = takeOut ? JSON.parse(takeOut) : []; //JSON   
    takeOut.push(choose.id); //  
    takeOut = new Set(takeOut); // 
    takeOut = Array.from(takeOut); //  array 
    localStorage.setItem("watched", JSON.stringify(takeOut));
  });

  useEffect(() => {
    if (isNaN(num) === true) {
      //     
      alert(" .");
    }
  });

  //App.js goods    .
  let { id } = useParams();
  console.log(id); // 
  //.find() array       .
  let choose = props.goods.find(function (a) {
    return a.id == id; //array.id == url      choose 
  });

  return (
    <div>
      <Container>
        <Row>
          <Col>
            <input
              onChange={(e) => {
                setNum(e.target.value);
              }}
            />
          </Col>
        </Row>
        <Row>
          <Col sm={6}>
            <img
              src={
                process.env.PUBLIC_URL +
                "/img/cont3_cake" +
                Number(choose.id) +
                ".png"
              }
              width="100%"
              alt="cake1"
            />
          </Col>
          <Col sm={6}>
            <h4>{choose.title}</h4>
            <p>{choose.content}</p>
            <p>{choose.price}</p>
            <Button
              variant="danger"
              onClick={() => {
                dispatch(
                  addItem({
                    id: choose.id,
                    img: choose.img,
                    name: choose.title,
                    price: choose.price,
                    count: 1,
                  })
                );
                Navigate("/Cart");
              }}
            >
              
            </Button>
            <OrangeBtn></OrangeBtn>
          </Col>
        </Row>
        {/* <Counter /> */}
      </Container>
      {/*   UI */}
      <Container>
        <Nav variant="tabs" defaultActiveKey="tab01">
          <Nav.Item>
            <Nav.Link
              eventKey="tab01"
              onClick={() => {
                setTab(0);
              }}
            >
              
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link
              eventKey="tab02"
              onClick={() => {
                setTab(1);
              }}
            >
              
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link
              eventKey="tab03"
              onClick={() => {
                setTab(2);
              }}
            >
              
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="disabled">// </Nav.Link>
          </Nav.Item>
        </Nav>
        <Tabcontent tab={tab} /> {/*  tab  html  */}
      </Container>
    </div>
  );
};

function Tabcontent({ tab }) {
  let [fade, setFade] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setFade("end");
    }, 500);
    return () => {
      setFade(""); //  
    };
  }, [tab]); //    
  if (tab === 0) {
    return <div className={`start ` + fade}></div>;
  }
  if (tab === 1) {
    return <div className={`start ` + fade}></div>;
  }
  if (tab === 2) {
    return <div className={`start ` + fade}>// </div>;
  }
  if (tab === 3) {
    return <div className={`start ` + fade}></div>;
  }
}

export default Detail;
